import React, { Component } from 'react'
import * as XLSX from "xlsx";
import DataTable from 'react-data-table-component';


export default class HomePage extends Component {
    constructor(props) {
        super(props);
        this.state = {
          data: {},

        };
    }

    async getData() {
        let filepath = process.env.PUBLIC_URL+"/data.xlsx"
        const workbook = await fetch(filepath)
        .then(resp => resp.arrayBuffer())
        .then(buff => {var x = XLSX.read(buff); this.processData(x)})
        .catch(err => console.error(err))
    }
    processData(data){
        console.log(data)
        var processed = data.Sheets.Sheet1;
        let json = XLSX.utils.sheet_to_json(processed);
        console.log(json);
        var institutes=[], rankings = []; 
        json.map(item=>{
            institutes[item["Institution                    "]]=Object.entries(item).slice(1,item.length);
            // rankings.push{}
        })
        console.log(institutes)
        this.setState({data:json})
    }
    render() {
        const columns = [
            {
                name: 'Name',
                selector: row => row['Institution']
            },
            {
                name: 'QS',
                selector: row => row.qs,
                sortable: true,
            },
            {
                name: 'THE',
                selector: row => row.THE,
                sortable: true,
            },
            {
                name: 'CS Ranking AI',
                selector: row => row['csranking #AI'],
                sortable: true,
            },
        ];
        
        return (
            <div>
                <button
                onClick={() => {
                    this.getData();
                }}
                >
                Read File
                </button>
                <DataTable
                    columns={columns}
                    data={this.state.data}
                />
            </div>
        );
    }
}
